.logo {
  width: 100vw;
}

.logo div {
  width: 100vw;
  display: flex;
  justify-content: center;
}

.logo div img {
  height: 150px;
}

.form {
  margin: 5% 20% 5%;
  box-shadow: 0px 4px 18px rgba(0, 0, 0, 0.10);
  border-radius: 20px 20px 20px 20px;
  background-color: white;
}

.form h1 {
  margin: auto;
  width: fit-content;
}

.form div {
  padding: 20px;
  width: auto;
  /* display: flex;
    flex-direction: column; */
}

form {
  display: flex;
  justify-content: center;
  flex-direction: column;
}

form .form-inp-field {
  /* display: flex; */
  width: auto;
  flex-wrap: wrap;
  justify-content: center;
}

.form input, .form select {
  padding: 20px 8px;
  width: 100%;
  border: 1px solid #faebd7;
  border-radius: 5px;
  box-shadow: 1px 1px 7px -5px black;
}

.form select {
  min-width: 200px;
}

.form #submit {
  width: 130px;
  margin: auto;
  color: white;
  font-weight: 700;
  font-size: medium;
  background: rgb(40 33 33 / 79%);
  box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25);
  border-radius: 5px;
  transition: ease-in .2s;
  border-color: #5a5a5a;
  cursor: pointer;
}

.form #submit:hover {
  background: rgba(22, 18, 18, 0.79);
  ;
}

@media only screen and (max-width: 1000px) {
  .form {

    margin: 5% 10% 5%;
  }

  .form div {
    padding: 15px;
  }

  .form input {
    padding: 10px 8px;
  }
}

